@import 'scss/container.scss';

body {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  // font-size: $mainFontSize;
  // color: $mainFontColor;
  background-color: #E5E5E5;
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  color: #000000;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

button {
  cursor: pointer;
}

li {
  list-style: none;
}